import React, { Component } from 'react';
import Data from '../api/Data';
  
import axios from 'axios';

import {
    Card, CardBody, CardTitle, CardSubtitle, Row, InputGroup, Input
} from "reactstrap";

import { Link } from 'react-router-dom';

class CountryList extends Component {

    state = {
        countries: []
    }

    componentDidMount() {
        axios.get("https://restcountries.eu/rest/v2/all").then(res => {
            this.setState({
                countries: res.data
            })
        });
    }

    inputHandler = (e) => {
        axios.get('https://restcountries.eu/rest/v2/name/'+ e.target.value)
        .then(res => {
            this.setState({
                countries: res.data
            })
        })
    }

    render() {

        const countries = this.state.countries.map((country, i) => ( country.name != "Armenia" ?   
            <Link to = {"/" + country.callingCodes[0]} className = "nav-link text-dark col-sm-3">
                <Card className = "mb-2">
                    <img width="100%" src={country.flag} alt="Card image cap" />
                    <CardBody>
                        <CardTitle tag="h5">{country.name}</CardTitle>
                        <CardSubtitle tag="h6" className="mb-2 text-muted">{country.nativeName}</CardSubtitle>
                    </CardBody>
                </Card>
            </Link> : ""
        ))

        return (
            <React.Fragment>
                <InputGroup className = 'my-2 mt-3'>
                    <Input name = "searchBar" onChange = {this.inputHandler}/>
                </InputGroup>
                <Row>
                    {countries}
                </Row>
            </React.Fragment>
        );
    }
}

export default CountryList;
