import React from 'react';
import {
    Card, CardBody, CardTitle, CardSubtitle 
} from "reactstrap";
import { Link } from 'react-router-dom';

import axios from 'axios';

class CountryCard extends React.Component{

    state = {
        currentCountry: {}
    }
    
    async componentDidMount() {
        const callingCode = this.props.match.params.name;
        await axios.get("https://restcountries.eu/rest/v2/callingcode/"+ callingCode)
        .then(res => {
            this.setState({
                currentCountry: res.data[0]
            })
        })
    }
    
    render() {
        const {name, nativeName, flag, population, region, capital} = this.state.currentCountry;
        return(
                <Card className = "my-4">
                    <CardBody className = "row">
                        <div className = "col-sm-6">
                            <CardTitle tag="h5">{name}</CardTitle>
                            <CardSubtitle tag="h6" className="mb-2 text-muted">{nativeName}</CardSubtitle>
                            <hr />
                            <CardSubtitle tag="h6" className="mb-2 text-muted">Capital: {capital}</CardSubtitle>
                            <CardSubtitle tag="h6" className="mb-2 text-muted">Region: {region}</CardSubtitle>
                            <CardSubtitle tag="h6" className="mb-2 text-muted">Population: {population}</CardSubtitle>
                        </div>
                        <div className = "col-sm-6">
                            <img src = {flag} className = "w-100" alt = "flag"/>
                        </div>
                    </CardBody>
                </Card>
        )
    }

}

export default CountryCard;